import React, { Component } from 'react';
import { View, Text } from 'react-native';
import { getTotalGlobal } from '../functions/getData'
import CardGlobal from '../components/CardGlobal'
import { Container, Header, Item, Icon, Button, Input, Left, Right, Body, Title } from 'native-base';
import { Actions } from 'react-native-router-flux'
export default class TotalGlobal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: null
        };
    }

    async getData() {
        let data = await getTotalGlobal()
        this.setState({ data })
        console.log(data)
    }

    componentDidMount() {
        this.getData()
    }

    render() {
        return (

            <View>
                <Header>
                    <Left>
                        <Button transparent onPress={() => Actions.replace('menuPrincipal')}>
                            <Icon name='arrow-back' />
                        </Button>
                    </Left>
                    <Body>
                        <Title>Total Global</Title>
                    </Body>
                    <Right>
                        <Button transparent>
                        </Button>
                    </Right>
                </Header>
                {
                    !this.state.data ? null :
                        <CardGlobal
                            newConfirmed={this.state.data.todayCases} //ok
                            newDeaths={this.state.data.todayDeaths} //ok
                            totalConfirmed={this.state.data.cases} //ok
                            totalRecovered={this.state.data.recovered} //ok
                            totalDeaths={this.state.data.deaths} //ok
                            actives={this.state.data.active}  //ok
                            critical={this.state.data.critical} //ok
                            casesPerOneMillion={this.state.data.casesPerOneMillion} //ok
                            deathsPerOneMillion={this.state.data.deathsPerOneMillion} //ok
                            tests={this.state.data.tests} //ok
                            testsPerOneMillion={this.state.data.testsPerOneMillion}//ok
                            population={this.state.data.population} //ok
                            activePerOneMillion={this.state.data.activePerOneMillion} //ok
                            recoveredPerOneMillion={this.state.data.recoveredPerOneMillion}//ok
                            criticalPerOneMillion={this.state.data.criticalPerOneMillion}
                            affectedCountries={this.state.data.affectedCountries} //ok 

                        />
                }

            </View>
        );
    }
}
