import React, { Component } from 'react';
import { View, Text, StyleSheet, FlatList, TouchableOpacity, Image, Platform } from 'react-native';
import { Card, Divider } from 'react-native-elements'
import { numberToReal } from '../functions/formatNumber'
import { Icon } from 'native-base';
import Grafico from './Grafico'
import { Actions } from 'react-native-router-flux'

export default class CardCountries extends Component {
    constructor(props) {
        super(props);
        this.state = {
            paisSelecionado: null
        };
    }


    componentWillReceiveProps(props) {
        console.log("PROPS ", props)
        if (props.dadosGrafico) {
            Actions.replace('mostraGraficos', { dadosGrafico: props.dadosGrafico })
        }
    }

    async selecionaPais(pais) {
        this.setState({ paisSelecionado: pais })
        this.props.paisSelecionado(pais)

    }

    componentesLista = ({ item }) => (


        <Card containerStyle={estilo.cardStyle} title={item.country + ' ' + item.countryInfo.iso2}>
            <View style={{ flex: 1, alignItems: 'center' }}>
                <Image source={{ uri: item.countryInfo.flag }} style={estilo.estiloImgWeb} />
            </View>

            <View style={estilo.viewPrincipal}>

                <View style={{ flex: 1, flexWrap: 'wrap' }}>

                    <View style={estilo.viewDataCountries}>
                        <Text style={estilo.label}>Population:
                            <Text style={estilo.goodNumbers}> {numberToReal(item.population)}</Text>
                        </Text>
                        <Text style={estilo.label}>Continent:
                            <Text style={estilo.goodNumbers}> {item.continent}</Text>
                        </Text>
                    </View>

                    <Divider style={estilo.estiloDivider} />

                    <View style={estilo.viewLine}>
                        <View style={estilo.viewBad}>
                            <Text style={estilo.label}>New Confirmed:
                                <Text style={estilo.numbersBad}> {numberToReal(item.todayCases)}</Text>
                            </Text>
                            <Text style={estilo.label}>New Deaths:
                                <Text style={estilo.numbersBad}>{numberToReal(item.todayDeaths)}</Text>
                            </Text>
                            <Text style={estilo.label}>Actives:
                                <Text style={estilo.numbersBad}>{numberToReal(item.active)}</Text>
                            </Text>
                            <Text style={estilo.label}>Critical:
                                <Text style={estilo.numbersBad}>{numberToReal(item.critical)}</Text>
                            </Text>
                            <Text style={estilo.label}>Critical Per One Million:
                                <Text style={estilo.numbersBad}>{numberToReal(item.criticalPerOneMillion)}</Text>
                            </Text>
                        </View>
                        <View>
                            <Text style={estilo.label}>Total Confirmed:
                                <Text style={estilo.goodNumbers}> {numberToReal(item.cases)}</Text>
                            </Text>
                            <Text style={estilo.label}>Total Deaths:
                                <Text style={estilo.goodNumbers}> {numberToReal(item.deaths)}</Text>
                            </Text>
                            <Text style={estilo.label}>Total Recovered:
                            <Text style={estilo.goodNumbers}> {numberToReal(item.recovered)}</Text>
                            </Text>
                            <Text style={estilo.label}>Recovered Per One Million:
                            <Text style={estilo.goodNumbers}> {numberToReal(item.recoveredPerOneMillion)}</Text>
                            </Text>
                        </View>

                        <View>
                            <Text style={estilo.label}>Cases Per One Million:
                            <Text style={estilo.goodNumbers}> {numberToReal(item.casesPerOneMillion)}</Text>
                            </Text>
                            <Text style={estilo.label}>Deaths Per One Million:
                            <Text style={estilo.goodNumbers}> {numberToReal(item.deathsPerOneMillion)}</Text>
                            </Text>
                            <Text style={estilo.label}>Tests:
                            <Text style={estilo.goodNumbers}> {numberToReal(item.tests)}</Text>
                            </Text>
                            <Text style={estilo.label}>Tests per One Million:
                            <Text style={estilo.goodNumbers}> {numberToReal(item.testsPerOneMillion)}</Text>
                            </Text>
                        </View>

                    </View>

                </View>

            </View>




            {/* <View style={estilo.opcoes}>
                <TouchableOpacity onPress={() => this.selecionaPais(item.Country)}>
                    <Icon name='pie' style={estilo.iconesOpcoes} color='#fff' />
                </TouchableOpacity>
            </View> */}


        </Card>

    )

    render() {
        return (
            <View style={{ flex: 1 }}>
                <FlatList
                    renderItem={this.componentesLista}
                    data={this.props.data}
                />
            </View>
        );
    }
}


const estilo = StyleSheet.create({
    viewPrincipal: {
        flexDirection: 'row',
        alignItems: 'center',
        flex: 1,
        flexWrap: 'wrap'
    },
    estiloDivider: {
        paddingVertical: 5,
        backgroundColor: '#81ecec',
        borderRadius: 10,
        marginVertical: 15
    },
    label: {
        fontWeight: 'bold',
        fontSize: 18,
        flex: 1,
        flexWrap: 'wrap',
    },
    numbersBad: {
        fontWeight: 'bold',
        fontSize: 18,
        color: '#e17055'
    },
    goodNumbers: {
        fontWeight: 'bold',
        fontSize: 18,
        color: '#00b894'
    },
    viewGood: {
        marginTop: 15,
        marginBottom: 15,
    },
    viewBad: {
        marginBottom: 15
    },
    cardStyle: {
        borderRadius: 10,
        elevation: 5,
        borderColor: '#b2bec3',
        flex: 1,
        flexWrap: 'wrap'
    },
    opcoes: {
        flex: 1,
        backgroundColor: '#34495e',
        borderRadius: 5,
        padding: 5
    },
    iconesOpcoes: {
        color: '#fff'
    },
    estiloImgWeb: {
        width: 300,
        height: 100,
        borderWidth: 1,
        borderColor: 'black',
        marginRight: 15,
        borderRadius: 7,

    },
    viewLine: {
        flexDirection: 'row',
        justifyContent: 'space-between',
        flex: 1,
        flexWrap: 'wrap',
    },
    viewDataCountries: {
        flexDirection: 'row',
        justifyContent: 'space-around',
        flex: 1,
        flexWrap: 'wrap',

    }
})