export function getTotalCountries() {
    return new Promise(result => {
        axios.get('https://corona.lmao.ninja/v2/countries')
            .then(resp => {
                result(resp.data)
            })
    })
}




export function getDayOne(pais) {
    return new Promise(result => {
        axios.get(`https://api.covid19api.com/dayone/country/${pais}/status/confirmed`)
            .then(resp => {
                result(resp.data)
            })
    })
}
