import React from 'react'

import Graficos from '../components/Grafico'
import { View } from 'native-base'

export default class MostraGraficos extends React.Component {

    constructor(props) {
        super(props)
    }

    render() {
        return (
            <View style={{ flex: 1 }}>
                <Graficos
                    dadosGrafico={this.props.dadosGrafico}
                />
            </View>
        )
    }
}