import React, { Component } from 'react';
import { View, Text, StyleSheet, ScrollView } from 'react-native';
import { Card, Divider } from 'react-native-elements'
import { numberToReal } from '../functions/formatNumber'

export default class CardGlobal extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    render() {
        return (
            <ScrollView style={{ flex: 1 }}>
                <Card containerStyle={estilo.cardStyle} title={!this.props.country ? null : this.props.country + ' ' + this.props.countryCode}>
                    <View>
                        <View style={estilo.viewLine}>
                            <Text style={estilo.label}>Affected Countries:
                                <Text style={estilo.goodNumbers}> {numberToReal(this.props.affectedCountries)}</Text>
                            </Text>
                            <Text style={estilo.label}>Population:
                                <Text style={estilo.goodNumbers}> {numberToReal(this.props.population)}</Text>
                            </Text>
                        </View>

                        <Divider style={estilo.estiloDivider} />

                        <View style={estilo.viewLine}>
                            <View style={estilo.viewBad}>
                                <Text style={estilo.label}>New Confirmed:
                                    <Text style={estilo.numbersBad}> {numberToReal(this.props.newConfirmed)}</Text>
                                </Text>
                                <Text style={estilo.label}>New Deaths:
                                    <Text style={estilo.numbersBad}>{numberToReal(this.props.newDeaths)}</Text>
                                </Text>
                                <Text style={estilo.label}>Actives:
                                    <Text style={estilo.numbersBad}>{numberToReal(this.props.actives)}</Text>
                                </Text>
                                <Text style={estilo.label}>Critical:
                                    <Text style={estilo.numbersBad}>{numberToReal(this.props.critical)}</Text>
                                </Text>

                                <Text style={estilo.label}>Actives Per One Million:
                                    <Text style={estilo.numbersBad}>{numberToReal(this.props.activePerOneMillion)}</Text>
                                </Text>

                            </View>

                            <View style={estilo.viewGood}>
                                <Text style={estilo.label}>Cases Per One Million:
                                    <Text style={estilo.goodNumbers}> {numberToReal(this.props.casesPerOneMillion)}</Text>
                                </Text>
                                <Text style={estilo.label}>Total Confirmed:
                                    <Text style={estilo.goodNumbers}> {numberToReal(this.props.totalConfirmed)}</Text>
                                </Text>
                                <Text style={estilo.label}>Total Deaths:
                                    <Text style={estilo.goodNumbers}> {numberToReal(this.props.totalDeaths)}</Text>
                                </Text>
                                <Text style={estilo.label}>Total Recovered:
                                    <Text style={estilo.goodNumbers}> {numberToReal(this.props.totalRecovered)}</Text>
                                </Text>
                                <Text style={estilo.label}>Recovered Per One Million:
                                    <Text style={estilo.goodNumbers}> {numberToReal(this.props.recoveredPerOneMillion)}</Text>
                                </Text>
                            </View>

                            <View style={estilo.viewGood}>
                                <Text style={estilo.label}>Critical Per One Million:
                                    <Text style={estilo.goodNumbers}> {numberToReal(this.props.criticalPerOneMillion)}</Text>
                                </Text>
                                <Text style={estilo.label}>Deaths Per One Million:
                                    <Text style={estilo.goodNumbers}> {numberToReal(this.props.deathsPerOneMillion)}</Text>
                                </Text>
                                <Text style={estilo.label}>Tests:
                                    <Text style={estilo.goodNumbers}> {numberToReal(this.props.tests)}</Text>
                                </Text>
                                <Text style={estilo.label}>Tests per One Million:
                                    <Text style={estilo.goodNumbers}> {numberToReal(this.props.testsPerOneMillion)}</Text>
                                </Text>
                                <Text style={estilo.label}>Total Recovered:
                            <Text style={estilo.goodNumbers}> {numberToReal(this.props.totalRecovered)}</Text></Text>
                            </View>
                        </View>


                    </View>
                </Card>
            </ScrollView>
        );
    }
}


const estilo = StyleSheet.create({
    estiloDivider: {
        paddingVertical: 5,
        backgroundColor: '#81ecec',
        borderRadius: 10
    },
    label: {
        fontWeight: 'bold',
        fontSize: 18
    },
    numbersBad: {
        fontWeight: 'bold',
        fontSize: 18,
        color: '#e17055'
    },
    goodNumbers: {
        fontWeight: 'bold',
        fontSize: 18,
        color: '#00b894'
    },
    viewGood: {
        marginTop: 15
    },
    viewBad: {
        marginBottom: 15,
        marginTop: 15
    },
    cardStyle: {
        borderRadius: 10,
        elevation: 5,
        borderColor: '#b2bec3',
    },
    viewLine: {
        flexDirection: 'row',
        justifyContent: 'space-between',
        flex: 1,
        flexWrap: 'wrap',
    },
})