import React from 'react'

import { View, Modal, TouchableOpacity, Dimensions, Text, StyleSheet, ScrollView } from 'react-native'

import {
    LineChart,
    BarChart,
    PieChart,
    ProgressChart,
    ContributionGraph,
    StackedBarChart
} from "react-native-chart-kit";
import { Header, Left, Button, Icon, Body, Title, Right } from 'native-base';
import { Actions } from 'react-native-router-flux'

export default class Grafico extends React.Component {

    constructor(props) {
        super(props)
        this.state = {

        }
    }

    render() {
        return (
            <ScrollView style={{ flex: 1, }}>
                <Header>
                    <Left>
                        <Button transparent onPress={() => Actions.replace('totalCountries', { dadosGrafico: false })}>
                            <Icon name='arrow-back' />
                        </Button>
                    </Left>
                    <Body>
                        <Title>Charts</Title>
                    </Body>
                    <Right>
                        <Button transparent>
                        </Button>
                    </Right>
                </Header>
                <View style={estilo.viewTitulo}>
                    <Text style={estilo.titulo}>Total Confirmed</Text>
                </View>
                <View style={estilo.viewGrafico}>


                    <LineChart
                        data={{
                            labels: this.props.dadosGrafico.label,
                            datasets: [
                                {
                                    data: this.props.dadosGrafico.valores
                                }
                            ]
                        }}
                        width={Dimensions.get("window").width} // from react-native
                        height={400}
                        yAxisLabel=""
                        yAxisSuffix=""
                        yAxisInterval={1000} // optional, defaults to 1
                        chartConfig={{
                            backgroundColor: "#130f40",
                            backgroundGradientFrom: "#30336b",
                            backgroundGradientTo: "#686de0",
                            decimalPlaces: 2, // optional, defaults to 2dp
                            color: (opacity = 1) => `rgba(255, 255, 255, ${opacity})`,
                            labelColor: (opacity = 1) => `rgba(255, 255, 255, ${opacity})`,
                            style: {
                                borderRadius: 16
                            },
                            propsForDots: {
                                r: "6",
                                strokeWidth: "2",
                                stroke: "#ffa726"
                            }
                        }}
                        bezier
                        style={{
                            marginVertical: 8,
                            borderRadius: 16,
                            marginRight: 20
                        }}
                    />
                </View>
            </ScrollView>
        )
    }
}


const estilo = StyleSheet.create({
    viewTitulo: {
        alignItems: 'center',
        backgroundColor: '#2980b9',
        borderRadius: 15,
        margin: 10
    },
    titulo: {
        color: '#fff',
        fontSize: 22
    },
    viewGrafico: {
        marginHorizontal: 10,
        marginRight: 15,
        flex: 1
    }
})