import React, { Component } from 'react';
import { View, Text, StyleSheet, Modal } from 'react-native';
import { getTotalCountries, getDayOne } from '../functions/getData'
import CardCountries from '../components/CardCountries'
import { Container, Header, Item, Icon, Button, Input, Left, Right, Title, Body } from 'native-base';
import moment from 'moment'
import { Actions } from 'react-native-router-flux'

export default class TotalCountries extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: null,
            dataOriginal: null,
            paisSelecionado: null,
            abrirModal: false,
            dadosParaGrafico: null
        };
    }

    async getData() {
        let data = await getTotalCountries()
        this.setState({ data })
        this.setState({ dataOriginal: data })
    }


    async getDayOne(pais) {


        this.setState({ paisSelecionado: pais })

        let data = await getDayOne(pais)
        let label = []
        let valores = []

        data.forEach(d => {


            label.push(moment.utc(d.Date).format('MM'))
            valores.push(d.Cases)
        })

        let obj = {
            label: label,
            valores: valores
        }


        this.setState({ dadosParaGrafico: obj })

    }

    pesquisa(texto) {
        let filtro = this.state.data
        console.log(filtro)
        if (texto) {
            filtro = filtro.filter(f => f.country.toLowerCase() == texto.toLowerCase())
            if (filtro.length > 0) {
                this.setState({ data: filtro })
            }
        } else {
            this.setState({ data: this.state.dataOriginal })
        }
    }

    componentDidMount() {
        this.getData()
    }

    render() {
        return (

            <View style={{ flex: 1 }}>
                <Header>
                    <Left>
                        <Button transparent onPress={() => Actions.replace('menuPrincipal')}>
                            <Icon name='arrow-back' />
                        </Button>
                    </Left>
                    <Body>
                        <Title>Total Por País</Title>
                    </Body>
                    <Right>
                        <Button transparent>
                        </Button>
                    </Right>
                </Header>
                <View>

                    <Header searchBar rounded>

                        <Item>
                            <Icon name="ios-search" />
                            <Input placeholder="Pesquise por País" onChangeText={(e) => this.pesquisa(e)} />
                            <Icon name="ios-flag" />
                        </Item>
                    </Header>
                </View>
                {
                    !this.state.data ? null :

                        <CardCountries
                            data={this.state.data}
                            paisSelecionado={(pais) => this.getDayOne(pais)}
                            dadosGrafico={this.state.dadosParaGrafico}

                        />
                }

            </View>
        );
    }
}
